:root {
  --primary-white-color: #fff;
  --test-background-color: #000004;
  --accent-blue-color: #007bff;
}

body {
  background-color: var(--test-background-color);
  color: var(--primary-white-color);
  letter-spacing: .02em;
  text-align: justify;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
}

.video-bg {
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: relative;
}

.video-bg video {
  z-index: 1;
  object-fit: cover;
  width: 100%;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
}

.effects {
  object-fit: cover;
  z-index: 2;
  background-color: #0009;
  width: 100%;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
}

.main-container {
  z-index: 2;
  max-width: 425px;
  margin: 0 auto;
  padding: 15px;
}

.lang {
  flex-direction: row;
  justify-content: flex-end;
  gap: 15px;
  font-size: 13px;
  display: flex;
}

.lang a {
  color: var(--primary-white-color);
  text-decoration: none;
}

.lang a:active {
  color: var(--accent-blue-color);
}

.english:hover, .ukrain:hover, .russian:hover {
  cursor: pointer;
}

.project-link {
  color: var(--accent-blue-color);
}

.project-link:active {
  color: var(--primary-white-color);
}

.about-title {
  text-align: center;
  margin: 0 auto;
  padding: 20px 0;
}

.myphoto {
  border-radius: 50%;
  width: 200px;
  height: 200px;
  margin: 0 auto 20px;
  overflow: hidden;
}

.myphoto img {
  object-fit: cover;
  object-position: 50% 50%;
  width: 100%;
  height: 100%;
  display: block;
}

.aboutme {
  font-size: 16px;
  font-weight: 400;
}

h2 {
  text-align: center;
  margin: 0 auto;
  padding: 20px 0;
}

.skills-items > li:not(:last-child) {
  margin-bottom: 15px;
}

.skills-items li strong {
  color: #007bff;
  font-size: 18px;
  font-weight: bold;
}

.whyme-items > li:not(:last-child) {
  margin-bottom: 15px;
}

.whyme-items li strong {
  color: #007bff;
  font-size: 18px;
  font-weight: bold;
}

.socials {
  margin-top: 30px;
}

.social-items {
  justify-content: center;
  align-items: flex-end;
  gap: 50px;
  padding-bottom: 20px;
  display: flex;
}

.social-icon-1, .social-icon-2, .social-icon-3 {
  fill: var(--primary-white-color);
}

.social-icon-3 {
  width: 55px;
  height: 55px;
  padding-bottom: 5px;
}

.social-icon-1:hover, .social-icon-1:focus {
  fill: red;
}

.social-icon-2:hover, .social-icon-2:focus {
  fill: #08c;
}

.social-icon-3:hover, .social-icon-3:focus {
  fill: #0077b5;
}

@media screen and (width >= 768px) {
  .main-container {
    max-width: 768px;
  }
}

@media screen and (width >= 1200px) {
  .main-container {
    max-width: 1200px;
    position: relative;
  }

  .aboutme, .skills, .mission, .whyme, .mycontacts, .socials, .project-title {
    padding: 20px;
    position: absolute;
  }

  .aboutme {
    width: 400px;
    top: 20vh;
    left: -55vh;
  }

  .skills {
    width: 650px;
    top: 60vh;
    left: -55vh;
  }

  .mission {
    width: 500px;
    top: 20vh;
    left: 57vh;
  }

  .whyme {
    width: 650px;
    top: 50vh;
    left: 40vh;
  }

  .project-title {
    width: 550px;
    top: 115vh;
    left: 45vh;
  }

  .mycontacts {
    width: 550px;
    top: 120vh;
    left: -40vh;
  }

  .socials {
    position: inherit;
    border: 0;
    margin-top: 0;
    top: 100vh;
    left: -10vw;
  }

  .social-items {
    padding-bottom: 0;
  }
}
